<template>
  <div class="container">
    <div class="dialog-box">
      <div class="users-box">
        <div class="box-header"><h3>Users</h3></div>
        <div class="box-content">
          <div class="box-element">
            <div class="avatar" style="background: lightgreen;">&#129437;</div>
            <div class="username">{{ me }}</div>
          </div>
          <div class="box-element">
            <div class="avatar" style="background:  #ffb3b3;" v-if="other">
              &#128058;
            </div>
            <div class="avatar" style="background:  #ffb3b3;" v-else>
              &#10067;
            </div>
            <div class="username" v-if="other">{{ other }}</div>
            <div class="username" v-else>Waiting... AUF!</div>
          </div>

          <div class="peer-box" v-if="!other">
            <div class="create" v-on:click="createPeer">Create Peer</div>
            <div class="connect" v-on:click="connectToPeer">
              Connect to Peer
            </div>
          </div>
        </div>
      </div>
      <div class="message-box">
        <div class="box-header"><h3>Messages</h3></div>
        <div class="messages" id="messages"></div>
        <form
          onsubmit="event.preventDefault();"
          class="input-form"
          v-if="other"
        >
          <input type="text" class="input-message" id="input-message" />
          <button v-on:click="sendMessage" style="display: none"></button>
          <div v-on:click="sendMessage" class="btn-send">&#9757;</div>
        </form>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
$bg: rgb(240, 240, 240);

.container {
  padding: 20px;
  margin: auto;
  max-width: 1000px;

  .dialog-box {
    display: flex;
    flex-wrap: wrap;

    .users-box {
      border: 1px solid lightgrey;
      width: 200px;
      flex-shrink: 0;
      flex-grow: 1;

      .box-content {
        padding: 10px 0;

        .box-element {
          display: flex;
          padding: 10px;
          margin: 10px 0;
          align-items: center;
          cursor: pointer;
          font-weight: 500;

          .avatar {
            width: fit-content;
            font-size: 1.5rem;
            padding: 10px;
            min-width: 2rem;
            text-align: center;
            border-radius: 50%;
            background: lightgreen;
          }

          .username {
            overflow: hidden;
            text-overflow: ellipsis;
            padding-left: 10px;
          }

          &:hover {
            background: $bg;
          }
        }

        .peer-box {
          display: flex;

          .create,
          .connect {
            width: fit-content;
            cursor: pointer;
            padding: 10px;
            margin: auto;
          }

          .create {
            background: rgb(255, 194, 82);

            &:hover {
              background: orange;
            }
          }

          .connect {
            background: lightgreen;

            &:hover {
              background: rgb(51, 216, 51);
            }
          }
        }
      }
    }

    .message-box {
      border: 1px solid lightgrey;
      display: block;
      flex-shrink: 0;
      width: 200px;
      flex-grow: 8;

      .messages {
        height: 500px;
        overflow-y: auto;
        padding: 10px;
        position: relative;
        display: flex;
        flex-direction: column;

        .message-other {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          position: relative;
          border: 1px solid #ffb3b3;
          border-radius: 1.2rem 1.2rem 1.2rem 0;
          width: fit-content;
          padding: 10px 37px 10px 10px;
          margin-bottom: 10px;

          .data {
            min-width: 50px;
            width: 100%;
            font-weight: 300;
          }

          .datetime {
            position: absolute;
            font-size: 0.6rem;
            font-weight: 300;
            right: 10px;
            bottom: 5px;
          }

          &::before {
            content: " ";
            position: absolute;
            width: 0;
            height: 0;
            bottom: -6px;
            left: -6px;
            transform: rotate(45deg);
            border: 5px solid;
            border-color: #ffb3b3 transparent transparent #ffb3b3;
          }

          &::after {
            content: " ";
            position: absolute;
            width: 0;
            height: 0;
            bottom: -5px;
            left: -4px;
            transform: rotate(45deg);
            border: 5px solid;
            border-color: #fff transparent transparent #fff;
          }
        }
        .message-my {
          display: flex;
          align-self: flex-end;
          flex-wrap: wrap;
          position: relative;
          border: 1px solid lightgreen;
          border-radius: 1.2rem 1.2rem 0 1.2rem;
          width: fit-content;
          padding: 10px 32px 10px 10px;
          margin-bottom: 10px;

          .data {
            min-width: 50px;
            width: 100%;
            font-weight: 300;
          }

          .datetime {
            position: absolute;
            font-size: 0.6rem;
            font-weight: 300;
            right: 5px;
            bottom: 5px;
          }

          &::before {
            content: " ";
            position: absolute;
            width: 0;
            height: 0;
            bottom: -6px;
            right: -6px;
            transform: rotate(45deg);
            border: 5px solid;
            border-color: lightgreen transparent transparent lightgreen;
          }

          &::after {
            content: " ";
            position: absolute;
            width: 0;
            height: 0;
            bottom: -5px;
            right: -4px;
            transform: rotate(45deg);
            border: 5px solid;
            border-color: #fff transparent transparent #fff;
          }
        }
      }

      .input-form {
        display: flex;
        border-top: 1px solid lightgrey;

        .input-message {
          flex-shrink: 0;
          width: 200px;
          flex-grow: 8;
          outline: none;
          border: none;
          font-size: 1.2rem;
          padding: 5px 10px;
        }

        .btn-send {
          cursor: pointer;
          display: block;
          flex-shrink: 0;
          flex-grow: 0;
          margin: 5px 10px;
          outline: none;
          border: none;
          font-size: 1.5rem;

          transition: all 0.5s ease-in-out;

          &:hover {
            animation: rep 500ms ease-in-out 0s infinite alternate;

            @keyframes rep {
              from {
                transform: rotate(-20deg);
              }
              to {
                transform: rotate(20deg);
              }
            }
          }
        }
      }
    }

    .box-header {
      padding: 20px;
      padding: 20px;
      background: $bg;

      h3 {
        font-weight: 500;
        text-align: center;
      }
    }
  }
}
</style>

<script>
import axios from "axios";
import Peer from "simple-peer";
import io from "socket.io-client";
const socket = io("https://authdiplom.herokuapp.com/");
// const socket = io("http://localhost:3000");
axios.defaults.baseURL = "http://localhost:3000/api/p2p";
axios.defaults.headers.common["Authorization"] = localStorage.jwt;

export default {
  name: "Profile",
  data: function() {
    return {
      peer: "",
      data: [],
      me: localStorage.username,
      other: "",
    };
  },
  methods: {
    createPeer: function() {
      this.peer = new Peer({ initiator: true });
      var i = 0;
      this.peer.on("signal", (data) => {
        this.data[i] = data;
        i++;
        if (i == 3)
          setTimeout(() => {
            if (i == 3)
              socket.emit("offerData", { message: JSON.stringify(this.data) });
          }, 1000);
        else if (i == 4)
          socket.emit("offerData", { message: JSON.stringify(this.data) });
      });

      this.peer.on("connect", () => {
        this.peer.send(JSON.stringify({ username: this.me }));
      });

      this.peer.on("error", (err) => console.error(err));

      this.peer.on("data", (data) => {
        if (JSON.parse(data.toString()).username)
          this.other = JSON.parse(data.toString()).username;
        if (JSON.parse(data.toString()).message) {
          document.getElementById(
            "messages"
          ).innerHTML += `<div class="message-other">
            <div class="data">${JSON.parse(data.toString()).message}</div>
            <div class="datetime">${JSON.parse(data.toString()).date}</div>
          </div>`;
          this.scroll();
        }
      });

      socket.on("answerData", (data) => {
        this.data = JSON.parse(data.message);
        this.data.forEach((e) => this.peer.signal(e));
      });
    },
    connectToPeer: function() {
      this.peer = new Peer();
      socket.on("offerData", (data) => {
        this.data = JSON.parse(data.message);
        this.data.forEach((e) => this.peer.signal(e));
      });

      var i = 0;
      this.peer.on("signal", (data) => {
        this.data[i] = data;
        i++;
        if (i == 3)
          setTimeout(() => {
            if (i == 3)
              socket.emit("answerData", { message: JSON.stringify(this.data) });
          }, 1000);
        else if (i == 4)
          socket.emit("answerData", { message: JSON.stringify(this.data) });
      });

      this.peer.on("connect", () => {
        this.peer.send(JSON.stringify({ username: this.me }));
      });

      this.peer.on("data", (data) => {
        if (JSON.parse(data.toString()).username)
          this.other = JSON.parse(data.toString()).username;
        if (JSON.parse(data.toString()).message) {
          document.getElementById(
            "messages"
          ).innerHTML += `<div class="message-other">
            <div class="data">${JSON.parse(data.toString()).message}</div>
            <div class="datetime">${JSON.parse(data.toString()).date}</div>
          </div>`;
          this.scroll();
        }
      });

      this.peer.on("error", (err) => console.error(err));
    },
    sendMessage: function() {
      let date = new Date();
      let message = document.getElementById("input-message").value;
      this.peer.send(
        JSON.stringify({
          message: message,
          date: date.toLocaleTimeString().substring(0, 5),
        })
      );
      document.getElementById("messages").innerHTML += `<div class="message-my">
            <div class="data">${message}</div>
            <div class="datetime">${date
              .toLocaleTimeString()
              .substring(0, 5)}</div>
          </div>`;
      this.scroll();
      document.getElementById("input-message").value = "";
    },
    scroll: function() {
      document.getElementById("messages").scrollTop = document.getElementById(
        "messages"
      ).scrollHeight;
    },
  },
};
</script>
